.checkout-item {
  width: 100%;
  display: flex;
  justify-content: space-around;
  min-height: 100px;
  border-bottom: 1px solid darkgrey;
  padding: 15px 0;
  font-size: 20px;
  align-items: center;

  .image-container {
    width: 23%;
    padding-right: 15px;

    img {
      width: 100%;
      height: 100%;
    }
  }
  .name,
  .quantity,
  .price {
    width: 23%;
  }

  .quantity {
    
    display:flex;
    justify-content: space-around;

      .arrow {
        cursor: pointer;
      }
  }

  .remove-button {
    padding-left: 12px;
    cursor: pointer;
  }
}
